@import 'core/variables';
@import 'core/reset';
.repo__list--item__link {
  text-decoration: none;
  &:hover {
    text-decoration: underline $bluerepo;
  }
  &--name {
    color: $bluerepo;
    font-weight: 700;
    font-size: 1.5em;
    margin: 4vh 0 2vh 0;
  }
}
.repo__list--item__flex {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: right;
  margin-top: 4vh;

  @media (min-width: 1024px) {
    flex-direction: row;
    justify-content: left;
    align-items: right;
  }
}
.repo__list--item__desc {
  margin: 4vh 0 2vh 0;
}
.repo__list--item__language {
  margin-right: 30px;
}
.visibility_span_label {
  background-color: $greyspan;
  font-weight: 400;
  font-size: 0.6em;
  color: $black;
  border-radius: 10px;
  text-decoration: none;
  padding: 0 5px 0 5px;
}
.language_span_label {
  display: inline-block;
  background-color: $greyspan;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  margin-right: 10px;
}
.license_span_label {
  display: inline-block;
  background-color: $bluespan;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  margin-right: 10px;
}
.update_span_label {
  display: inline-block;
  background-color: yellowgreen;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  margin-right: 10px;
}
.hidden {
  display: none;
}
